@keyframes loading {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(1200%);
  }
}
@keyframes stackSlide {
  from {
    right: -100%;
  }
  to {
    right: 0;
  }
}
@keyframes SlideMenu {
  from {
    left: -100%;
  }
  to {
    left: 0;
  }
}
@keyframes moveDown {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes moveUp {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}
