body {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.4rem;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Nunito Sans", sans-serif;
  margin: 0 0 2rem;
  color: #333;
  font-weight: 700;
  line-height: 1.4;
}
h1.white, h2.white, h3.white, h4.white, h5.white, h6.white {
  color: #fff;
}
h1.light, h2.light, h3.light, h4.light, h5.light, h6.light {
  font-weight: 300;
}
h1.normal, h2.normal, h3.normal, h4.normal, h5.normal, h6.normal {
  font-weight: normal;
}
h1.semi, h2.semi, h3.semi, h4.semi, h5.semi, h6.semi {
  font-weight: 500;
}
h1.capitalize, h2.capitalize, h3.capitalize, h4.capitalize, h5.capitalize, h6.capitalize {
  text-transform: capitalize;
}

p {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.4rem;
  color: #333333;
  margin-bottom: 2rem;
  line-height: 1.4;
}
p.white {
  color: #fff;
}
p.small {
  font-size: 1.2rem;
}
p.medium {
  font-size: 1.4rem;
}
p.big {
  font-size: 1.8rem;
}
p.light {
  font-weight: 300;
}
p.semi {
  font-weight: 500;
}
p.bold {
  font-weight: 700;
}

.btn {
  font-size: 1.1rem;
  border: none;
  outline: none;
  line-height: 1;
  box-shadow: none;
  border-radius: 0;
  color: #fff;
  padding: 1rem 1.5rem;
  background: #65c73b;
  font-weight: 500;
  transition: all 0.4s ease-out;
}
.btn.radius {
  border-radius: 0.4rem;
}
.btn.btn-large {
  font-size: 1.6rem;
  padding: 1.2rem 2rem;
}

.section {
  padding: 6rem 0;
}
.section.sm {
  padding: 4rem 0;
}
.section.md {
  padding: 5rem 0;
}
.section.xl {
  padding: 8rem 0;
}
.section.xxl {
  padding: 10rem 0;
}
.section.white {
  background: white;
}
.section.dark {
  background: #999;
}
.section.primary {
  background: #65c73b;
}
.section.secondary {
  background: #569e3b;
}
.section.light {
  background: #f7f7f7;
}

.size36 {
  font-size: 3.6rem;
}

.size30 {
  font-size: 3rem;
}

.size27 {
  font-size: 2.7rem;
}

.size24 {
  font-size: 2.4rem;
}

.size21 {
  font-size: 2.1rem;
}

.size18 {
  font-size: 1.8rem;
}

.custom-form {
  padding: 0;
}
.custom-form .field {
  margin-bottom: 2rem;
}
.custom-form .field label {
  font-weight: 600;
}
.custom-form .field .input input,
.custom-form .field .selection {
  font-size: 1.4rem;
  line-height: 2.6rem;
  padding: 1rem 2rem;
  border-color: #cccccc;
  border-radius: 0 !important;
}
.custom-form .field .input input .icon,
.custom-form .field .selection .icon {
  top: 1.3rem;
}
.custom-form .field .prompt.label {
  font-weight: 700;
  font-size: 1.2rem;
}
.custom-form .field .errorMsg {
  display: block;
  font-size: 1.3rem;
  color: red;
  text-align: right;
}

.mainHeader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  right: 30rem;
  display: flex;
  background: white;
}
.mainHeader.inner {
  right: 0;
  display: flex;
  padding: 0 1.5rem;
  align-items: center;
  padding-left: 25rem;
}
.mainHeader__logo {
  display: flex;
  margin-left: 1rem;
  padding: 1.5rem 0;
  align-items: center;
}
.mainHeader__logo img {
  display: block;
  width: 12.5rem;
}
.mainHeader__logo .text {
  font-size: 1.8rem;
  line-height: 3rem;
}
.mainHeader__menu {
  display: flex;
  padding: 0 1.5rem;
  align-items: center;
  margin-left: auto;
}
.mainHeader__menu ul {
  margin: 0;
  display: flex;
  align-items: center;
}
.mainHeader__menu ul li {
  position: relative;
  display: block;
}
.mainHeader__menu ul li.homeIcon img {
  width: 2rem;
}
.mainHeader__menu ul li.homeIcon a:after {
  display: none;
}
.mainHeader__menu ul li.homeIcon:hover img {
  transform: scale(1.05);
}
.mainHeader__menu ul li i {
  display: block;
}
.mainHeader__menu ul li a {
  display: block;
  padding: 1rem 2rem;
  color: #999;
  font-weight: 600;
}
.mainHeader__menu ul li a:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 100%;
  width: 0;
  height: 2px;
  transform: translateX(-50%);
  background-color: #65c73b;
  transition: all 0.2s ease-in-out;
}
.mainHeader__menu ul li:hover a {
  color: #65c73b;
}
.mainHeader__menu ul li:hover a:after {
  width: 40%;
}

.searchInputForm {
  flex-grow: 1;
  margin-left: 2rem;
}
.searchInputForm form {
  display: flex;
  padding: 1rem 0;
  align-items: center;
}
.searchInputForm form i {
  display: block;
}
.searchInputForm form .form-control {
  border: none;
  box-shadow: none;
}

.dashboardMenus {
  display: flex;
  flex-shrink: 0;
  margin-left: auto;
  align-items: center;
}
.dashboardMenus .link {
  padding: 1rem;
}
.dashboardMenus .link i {
  display: block;
  color: rgba(153, 153, 153, 0.5);
}
.dashboardMenus .link i:hover {
  color: #65c73b;
}
.dashboardMenus .profileWidget {
  position: relative;
  margin-left: 1rem;
}
.dashboardMenus .profileWidget .dropdown-toggle {
  border: none;
  outline: none;
  padding: 1.1rem 1rem;
  background: none;
}
.dashboardMenus .profileWidget .dropdown-menu {
  border: none;
  border-radius: 0;
  box-shadow: none;
}
.dashboardMenus .profileWidget .dropdown-menu a {
  padding: 1rem;
}
.dashboardMenus .profileWidget .dropdown-menu .divider {
  margin: 2px 0;
}

.wrapper {
  padding: 0;
  overflow: hidden;
}

.sidebar {
  position: relative;
  z-index: 101;
  width: 25rem;
  flex-shrink: 0;
  background: white;
  min-height: calc(100vh - 54px);
}
.sidebar .logoBox {
  display: flex;
  padding: 1rem;
  justify-content: center;
}
.sidebar .logoBox img {
  margin-top: -54px;
}
.sidebar__nav {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
}
.sidebar__nav a {
  position: relative;
  z-index: 1;
  display: flex;
  font-size: 15px;
  padding: 1.2rem 2rem;
  color: #999;
  align-items: center;
  transition: none;
  justify-content: space-between;
  border-bottom: 1px solid rgba(153, 153, 153, 0.2);
}
.sidebar__nav a:last-child {
  border-bottom: none;
}
.sidebar__nav a:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  z-index: -1;
  background-color: #65c73b;
  transition: width 100ms ease-out;
  border-radius: 0 25px 25px 0;
}
.sidebar__nav a .material-icons {
  font-size: 18px;
  color: #c0b1c2;
}
.sidebar__nav a:hover, .sidebar__nav a.active {
  color: white;
}
.sidebar__nav a:hover .material-icons, .sidebar__nav a.active .material-icons {
  color: white;
}
.sidebar__nav a:hover::before, .sidebar__nav a.active::before {
  width: 98%;
}

.loginLandingScreen {
  position: relative;
  z-index: 1;
  height: 100vh;
  padding-top: 8rem;
  padding-right: 30rem;
  overflow: hidden;
}
.loginLandingScreen .cloudsBlue {
  position: absolute;
  bottom: -5px;
  left: 0;
  z-index: -1;
}
.loginLandingScreen .cloudsBlue img {
  display: block;
  width: 80%;
}
.loginLandingScreen .cloudsGreen {
  position: absolute;
  bottom: -5px;
  right: 0;
  z-index: -2;
  display: flex;
  justify-content: flex-end;
}
.loginLandingScreen .cloudsGreen img {
  display: block;
  max-width: 25rem;
}
.loginLandingScreen .pencilRocket {
  position: absolute;
  left: 10%;
  bottom: 3rem;
  z-index: -1;
}
.loginLandingScreen .pencilRocket img {
  width: 40rem;
  transform: rotate(10deg);
}
.loginLandingScreen .contentBox {
  padding: 2rem 10rem;
  text-align: center;
}
.loginLandingScreen .contentBox .titleText {
  position: relative;
  z-index: 1;
  font-size: 4rem;
  font-weight: 600;
  color: #65c73b;
  text-transform: uppercase;
  margin-bottom: 2rem;
}
.loginLandingScreen .contentBox .titleText:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 100%;
  width: 10rem;
  height: 4px;
  margin-left: -5rem;
  background-color: #65c73b;
}
.loginLandingScreen .contentBox p {
  font-size: 1.6rem;
  color: #000;
}
.loginLandingScreen .loginSection {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -3;
  width: 30rem;
  height: 100vh;
  background: #eff9eb;
}
.loginLandingScreen .loginForm {
  text-align: center;
  padding: 12rem 8rem 0 3rem;
}
.loginLandingScreen .loginForm .btn-primary {
  border-radius: 4px;
  background: #569e3b;
}
.loginLandingScreen .loginForm .inputGroup {
